.container {
  height: 360px;
  border-bottom: 1px solid #ccc; }

.formsection {
  width: 710px;
  float: left; }

.logfacesection {
  margin-left: 710px;
  border-left: 1px solid #000; }

.logicon {
  width: 180px;
  height: 200px;
  margin-left: 190px;
  text-align: center; }
  .logicon img {
    width: 100px;
    height: 100px;
    margin: 15px auto;
    border: 5px solid #000;
    border-radius: 50%; }
  .logicon span {
    margin: 0 auto;
    font-size: 16px;
    color: #ccc; }
    .logicon span em {
      color: #000; }

dl {
  font-size: 16px; }
  dl dt {
    float: left;
    width: 145px;
    height: 70px;
    line-height: 70px;
    text-align: right; }
  dl dd {
    height: 70px;
    line-height: 70px;
    margin-left: 170px; }
    dl dd input {
      float: left;
      height: 31px;
      width: 310px;
      margin-top: 20px;
      text-indent: 15px;
      border: 1px solid #000;
      background-color: #fff; }

button {
  height: 30px;
  width: 310px;
  margin-top: 20px;
  color: #fff;
  background-color: #000;
  border: 0; }

.getver {
  position: absolute;
  width: 60px;
  margin-left: -70px;
  text-align: center;
  font-size: 12px; }

.error {
  display: inline-block;
  color: red;
  text-indent: 20px; }
